.button {
    min-width: 140px;
    border: none;
    outline: none;
    color: #20222c;
    color: var(--main-bg-color);
    background: #f8f8f2;
    background: var(--main-font-color);
    cursor: pointer;
    position: relative;
    z-index: 0;
    border-radius: 10px;
    margin : 5px;
    box-shadow: 5px 5px 10px black;
    padding: 5px;

}

.button:before {
    content: '';
    background: linear-gradient(90deg, #8be9fd, #f1fa8c, #ffb86c, #ff79c6, #50fa7b);
    background: linear-gradient(90deg, var(--main-font-color-comp1), var(--main-font-color-comp2), var(--main-font-color-comp3), var(--main-font-color-comp4), var(--main-font-color-comp5));
    position: absolute;
    top: -2px;
    left:-2px;
    background-size: 400%;
    z-index: -1;
    -webkit-filter: blur(5px);
            filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    -webkit-animation: glowing 20s linear infinite;
            animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
}

.button:active {
  background: #f8f8f2;
  background: var(--main-font-color);
}

.button:active:after {
    background: transparent;
}

.button:hover:before {
    opacity: 1;
}

.button:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #f8f8f2;
    background: var(--main-font-color);
    left: 0;
    top: 0;
    border-radius: 10px;
}

@-webkit-keyframes glowing {
    0% { background-position: 0 0; }
    50% { background-position: 400% 0; }
    100% { background-position: 0 0; }
}

@keyframes glowing {
    0% { background-position: 0 0; }
    50% { background-position: 400% 0; }
    100% { background-position: 0 0; }
}



